@keyframes slideIn{
    0% {
        transform: translateX(-100%);
        opacity: 0;
    }
    100% {
        transform: translateX(0);
        opacity: 1;
    }
}
@keyframes slideOut{
    0% {
        transform: translateX(0);
        opacity: 1;
    }
    100% {
        transform: translateX(-100%);
        opacity: 0;
    }
}
@keyframes slideInReversLogIn{
    0% {
        transform: translateX(30%);
        opacity: 0;
    }
    100% {
        transform: translateX(0%);
        opacity: 1;
    }
}
@keyframes slideInRegister{
    0% {
        transform: translateX(-30%);
        opacity: 0;
    }
    100% {
        transform: translateX(0);
        opacity: 1;
    }
}

@keyframes dropUp{
    0%{
        transform: translateY(0);
    }
    100%{
        transform: translateY(-500%);
        
    }
}
@keyframes dropDown{
    0%{
        transform: translateY(-100%);
    }
    100%{
        transform: translateY(0);
    }
}

@keyframes zoom{
    0%{transform: scale(1);}
    50%{transform: scale(1.45)} 
    100%{transform: scale(1);}
}

@keyframes borderAnimation {
    0%, 100% {
        border-bottom-color: #4fba6a; 
    }
    50% {
        border-bottom-color: transparent; 
    }
}

@keyframes fadeOut{
    0%{opacity: 1;}
    100%{opacity: 0}
}
@keyframes fadeIn{
    0%{opacity: 0;}
    100%{opacity: 1}
}

@keyframes rotation {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }

@keyframes activeTabMenu {
    0% {
        background: var(--DarkBlue);
        border: none;
        border-radius: 5px 50px 0 5px;
        padding: 10px 30px;
        color: var(--whiteColorDeam);
        text-align: center;
        letter-spacing: 0.5px;
        margin: 0 5px;
    }
    100% {
        background: var(--ButtonBlue);
        box-shadow: 6px 0px 2px #4c4c4c;
        padding: 10px 50px;
        transition: padding 0.2s ease;
    }
}