@import "../animations.css";

/*AreYouSure*/
.are-you-sure-bcg {
    width: 100%;
    height: 100%;
    position: fixed;
    background: rgba(42, 50, 51, 0.66);
    top: 0;
    left: 0;
    right: 0;
    z-index: 999;
}
.are-you-sure-bcg .are-yousure-main {
    position: fixed;
    max-width: 500px;
    height: auto;
    margin: 0 auto;
    left: 0;
    right: 0;
    top: 25%;
    background: var(--DarkBlue);
    z-index: 9999;
    padding: 10px;
    border-radius: 10px;
    box-shadow: 2px 2px 10px black;
}
.are-you-sure-bcg .are-yousure-main .title, .are-you-sure-bcg .are-yousure-main h3 {
    width: 100%;
    margin: 5px auto;
    color: white;
    text-align: center;
    letter-spacing: 1px;
}
.are-you-sure-bcg .are-yousure-main .answers.flex {
    width: 100%;
}
.are-you-sure-bcg .are-yousure-main .answers.flex button {
    padding: 10px 30px;
    background: var(--ButtonBlue);
    border: none;
    border-radius: 5px;
    color: var(--whiteColorDeam);
    text-align: center;
    font-weight: bold;
    letter-spacing: 1px;
    transition: 0.5s ease-in-out;
    margin: 10px auto;
}
.are-you-sure-bcg .are-yousure-main .answers.flex button:hover {
    transform: scale(1.1);
    transition: 0.3s ease-in-out;
    background: var(--NewLightBlue);
}

/*ComponentTitle*/
.component-title-container.flex {
    justify-content: space-between;
    margin: 0 auto;
    border-bottom: 3px dotted var(--GreenColor);
    padding: 5px;
    width: 100%;
}
.component-title-container.flex .title{
    width: 50%;
}
.component-title-container.flex .title h1 {
    letter-spacing: 1.5px;
}
.component-title-container.flex h4 {
    font-size: 15px;
    max-width: 200px;
    max-height: 50px;
    overflow: hidden;
    text-overflow:ellipsis;
    white-space:nowrap

}
.component-title-container.flex .breadcrumbs.flex {
    justify-content: center;
    overflow: hidden;
    text-overflow: ellipsis;
}
.component-title-container.flex .breadcrumbs.flex h4 a.active {
    color: var(--ButtonBlue);
    overflow: hidden;
    text-overflow:ellipsis;
    white-space:nowrap
}
.component-title-container.flex .breadcrumbs.flex .breadicon {
    font-size: 13px;
    margin: 0 5px;
}
.breadcrumb-notification-container{
    width: 50%;
    height: 100%;
    display: flex;
}
@media screen and (max-width: 700px){
    .breadcrumb-notification-container{
        display: block !important;
    }
    .component-title-container.flex{
        flex-direction: column !important;
    }
    .component-title-container.flex .breadcrumbs.flex{
        justify-content: left !important;
    }
    .component-title-container.flex .title{
        width: 100%;
        text-align: left !important;
    }
    .component-title-container.flex .breadcrumb-notification-container{
        width: 100%;
    }
}

/*NavMenu*/
.nav-menu-circle.flex {
    width: 60px;
    height: 60px;
    border: none;
    border-radius: 50px;
    background: var(--ButtonBlue);
    color: var(--whiteColorDeam);
    text-align: center;
    justify-content: center;
    left: 0;
    top: 100px;
    position: fixed;
    margin: 50px 100px;
    cursor: pointer;
}
.nav-menu-circle.flex .nav-icon {
    margin: 0 auto;
    font-size: 25px;
}

.nav-main-conatiner.flex {
    width: 200px;
    height: 100%;
    flex-direction: column;
    position: absolute;
    margin: 95px 95px;
    border: 1px 1px 1px 0px solid var(--GreyColor);
    background: var(--NavBgLightBlue);
    left: 0;
    top: 0px;
}
.nav-main-conatiner.flex .nav-header.flex {
    width: 100%;
    height: auto;
    justify-content: center;
    border-bottom: 2px dotted var(--GreenColor);
}
.nav-main-conatiner.flex .links {
    width: 100%;
    height: auto;
    text-align: center;
}
.nav-main-conatiner.flex .links li {
    padding: 10px;
    color: var(--DarkBlue);
}
.nav-main-conatiner.flex .links li:hover {
    background: var(--ButtonBlue);
    color: var(--whiteColorDeam);
}
.nav-main-conatiner.flex .nav-icon {
    margin: 0 10px;
    font-size: 20px;
}
.nav-main-conatiner.flex .nav-icon:hover {
    cursor: pointer;
}

/*Select*/
.select-container {
    width: 420px;
    height: fit-content;
    background: white;
    cursor: pointer;
    color: var(--DarkBlue);
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
}
.select-container .selected-option.flex {
    width: 100%;
    padding: 5px 0;
    background: white;
    text-align: center;
}
.select-container .selected-option.flex h4 {
    margin: 0 10px;
    width: 100%;
}
.select-container .active.flex {
    background-color: var(--GreenColor);
    width: 100%;
    padding: 5px 0;
}
.select-container .active.flex h4 {
    color: var(--DarkBlue) !important;
    margin: 0 10px;
    width: 100%;
}
.select-container .disabled.flex {
    background: var(--BackgroundColor);
    width: 100%;
    padding: 5px 0;
    cursor: default;
}
.select-container .disabled.flex h4 {
    color: unset;
    margin: 0 10px;
    width: 100%;
}
.select-container .select-icon {
    margin: 0 auto;
    margin-right: 15px;
}
.select-container .dropdown-menu {
    background-color: var(--GreyTransparent);
    border: 1px solid var(--DarkBlue);
    border-top: none;
    height: 80px;
    overflow: auto;
}
.select-container .dropdown-menu .dropdown-option {
    padding: 5px;
    text-align: center;
}
.select-container .dropdown-menu .dropdown-option:hover {
    background-color: var(--LightBlue);
    color: white;
}
/*SideMenu*/
.side-main-conainer {
    width: 90px;
    /*height: 100%;*/
    /*position: absolute;*/
    position: fixed;
    background: var(--BackgroundColor);
    /*top: 92px;*/ /* header icon height: 91.4 px + contentTitle 91 */
    box-shadow: 2px 5px 3px #888;
    /*border-radius: 0 0px 5px 0;*/
    border-radius: 0 30px 10px 0;
    /*border-left: none;
    border-top: none;*/
    border: 2px solid var(--DarkBlue);
    border-left: none;
    left: -15px;
    opacity: .7;
    transition: .5s ease-in-out;
}
.side-main-conainer:hover{
    left: 0;
    opacity: 1;
}
@media screen and (max-width: 1200px){
    .side-main-conainer{
        display: none;
    }
    .mobile-sidemenu-container{
        width: 90px;
        height: 100%;
    }
    .mobile-sidemenu-container .mobile-menu-button-container{
        height: 50px;
        margin: 0 auto;
        width: 100%;
    }
    .mobile-sidemenu-container .mobile-menu-button-container .mobile-menu-open-icon{
        font-size: 35px;
        color: var(--DarkBlue);
        margin: 0 auto;
        padding: 5px 5px;
        transition: .2s ease-in-out;
    }
    .mobile-menu-open-icon:hover{
        background: var(--ButtonBlue);
        border-radius: 10px;
        color: white !important;
        cursor: pointer;
    }
    .mobile-sidemenu-container .mobile-side-main-conainer{
        background: var(--BackgroundColor);
        box-shadow: 2px 5px 3px #888;
        /*border-radius: 0 0 5px 0;*/
        border-radius: 0 20px 20px 0;
        border-left: none !important;
        border: 2px solid var(--DarkBlue);
        width: 90px;
        /*height: 100%;*/
        position: fixed;
        z-index: 999;
    }
    .mobile-sidemenu-container .mobile-side-main-conainer .icon-container{
        text-align: center;
        margin: 0 auto;
        width: 100%;
        border-bottom: 1px dotted var(--DarkBlue);
    }
    .mobile-sidemenu-container .mobile-side-main-conainer .icon-container .menu-icon{
        text-align: center;
        padding: 10px;
        transition: .2s ease-in-out;
    }
    .mobile-sidemenu-container .mobile-side-main-conainer .icon-container .menu-icon:hover{
        background: var(--ButtonBlue);
        border-radius: 10px;
        color: white !important;
        cursor: pointer;
    }
}
@media screen and (max-width: 480px) {
    .mobile-sidemenu-container{
        display: none;
    }
}

@media screen and (min-width: 1200px){
    .mobile-sidemenu-container{
       display: none;
    }
}
.side-main-conainer .element-container.flex {
    width: 95%;
    height: fit-content;
    flex-direction: column;
    text-align: center;
    border-bottom: 2px dotted var(--GreyColor);
    padding: 10px 0;
    transition: 0.5s ease-in-out;
}
.side-main-conainer .element-container.flex .icon-container {
    width: 60px;
    min-height: 60px;
    color: var(--DarkBlue);
    font-weight: 600;
    display: flex;
    align-items: center;
}
.side-main-conainer .element-container.flex .icon-container .icon {
    margin: 0 auto;
    padding: 15px;
}
.side-main-conainer .element-container.flex .active .icon-container {
    border-radius: 50%;
    background: var(--ButtonBlue);
    color: var(--whiteColorDeam);
}
.side-main-conainer .element-container.flex .name-container {
    width: 100%;
}
.side-main-conainer .element-container.flex .name-container h4 {
    margin: 0;
    color: var(--PrimaryColor);
    font-size: 11px;
}
.side-main-conainer .element-container.flex:hover .icon-container {
    border-radius: 50%;
    background: var(--ButtonBlue);
    color: var(--whiteColorDeam);
    transition: 0.5s ease-in-out;
}
.side-main-conainer .menu-icon:hover {
    transform: scale(1.2);
    transition: 0.5s ease-in-out;
}

/*Table*/
.table-main-container {
    height: 500px;
    margin: 1% auto;
    width: 100%;
    overflow: auto;
}
.table-main-container .table-action-menu.flex {
    max-width: 100%;
    padding: 20px;
    flex-flow: row-reverse;
}
.table-main-container .table-action-menu.flex button {
    padding: 10px 20px;
    background: var(--ButtonBlue);
    color: var(--whiteColorDeam);
    border-radius: 10px;
    border: none;
    letter-spacing: 0.5px;
    box-shadow: 2px 2px 4px #4c4c4c;
    margin: 0 5px;
}
.table-main-container .table-action-menu.flex button:hover {
    transform: scale(1.1);
    transition: 0.3s ease-in-out;
}
.table-main-container .table-action-menu.flex button .table-menu-icon {
    padding: 0 5px;
}
.table-main-container table {
    border-spacing: 0;
    width: 100%;
    table-layout: auto;
}
.table-main-container table thead tr th {
    padding: 15px;
    text-align: left;
    letter-spacing: 0.5px;
    border-bottom: 2px solid var(--PrimaryColor);
    background: var(--DarkBlue);
    color: var(--whiteColorDeam);
}
.table-main-container table thead tr th td {
    display: flex;
    align-items: center;
}
.table-main-container table tbody tr:nth-child(2n) {
    background: var(--GreyTransparent);
}
.table-main-container table tbody tr td {
    padding: 10px;
    max-width: 300px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}
.table-main-container table tbody tr:hover {
    background-color: var(--LightBlueTransparent);
    cursor: pointer;
}
.table-main-container table tbody tr.disableSelection{
    cursor: default !important;
}
.table-main-container table tbody tr.active {
    background-color: var(--LightBlueTransparent);
}
.table-main-container .pagination-container.flex {
    font-size: 12px;
    color: var(--PrimaryColor);
    padding: 20px;
    justify-content: space-between;
    width: auto;
}
.table-main-container .pagination-container.flex .hidden-button{
    background: none;
    border: none;
}
.table-main-container .pagination-container.flex .show-container {
    min-width: 200px;
    padding: 5px;
}
.table-main-container .pagination-container.flex .show-container input {
    border-radius: 5px;
    width: 50px;
    height: 23px;
    border: none;
    margin: 0 5%;
    background-color: var(--ButtonBlue);
    color: var(--whiteColorDeam);
    cursor: var(--whiteColorDeam);
}
.table-main-container .pagination-container.flex .pagination.flex {
    width: fit-content;
    font-size: 13px;
}
.table-main-container .pagination-container.flex .pagination.flex .table-icon {
    font-size: 17px;
    margin: 0 8px;
}

.loader.table {
    position: absolute !important;
    left: 0 !important;
    right: 0 !important;
    top: 50% !important;
    margin: 0 auto;
    border-top: 4px solid var(--DarkBlue) !important;
}

/*TabMnu*/
.tabMenu-container.flex {
    margin: 20px auto;
    flex-wrap: wrap;
}
.tabMenu-container.flex .action {
    background: var(--DarkBlue);
    border: none;
    border-radius: 5px 50px 0 5px;
    padding: 10px 30px;
    color: var(--whiteColorDeam);
    text-align: center;
    letter-spacing: 0.5px;
    margin: 0 5px;
    transition: 0.5s ease-in-out;
}
.tabMenu-container.flex .action:hover {
    background: var(--ButtonBlue);
    transition: 0.3s ease-in-out;
}
.tabMenu-container.flex .action .icon {
    font-size: 17px;
    margin: auto 5px;
}

.tabMenu-container .active > .btn.action {
    background: var(--ButtonBlue);
    box-shadow: 6px 0px 2px #4c4c4c;
    padding: 10px 50px;
    animation: activeTabMenu 0.7s ease-in-out;
}
@media screen and (max-width: 700px){
    .tabMenu-container.flex .action{
        margin: 5px 5px !important;
        min-width: 200px;
    }
}

/*Calendar*/
.fc .fc-day-today {
    background-color: transparent !important;
}
.fc .fc-scrollgrid-section-sticky > *{
    background: transparent !important;
}

/*Notification*/
.notification-transition-container{
    position: fixed;
    top: 20px;
    right: 10px;
    width: 30%;
    height: auto;
    padding: 10px;
    background: var(--PrimaryColor);
    border-radius: 20px;
    animation: slideInRight 0.7s ease-in-out;
    box-shadow: 6px 6px 10px #888888;
    border: 1px solid var(--ButtonBlue);
}
.notification-transition-container .close-notification{
    width: 100%;
    height: 20px;
}
.notification-transition-container .close-notification .close-icon{
    color: white;
    position: relative;
    left: 90%;
    height: 100%;
    cursor: pointer;
    transition: .3s ease-in-out;
    font-size: 20px;
}
.notification-transition-container .close-notification .close-icon:hover{
    color: red;
    font-weight: bold;
    transform: scale(1.2);
}
.notification-transition-container .notification-icon-container{
    width:auto;
    height: 100%;
}
.notification-transition-container .notification-icon-container .notification {
    font-size: 40px;
    color: var(--DangerColorSecound);
    margin:0 auto;
    height: 100%;
}
.notification-transition-container .notification-message-container{
    height: 100%;
    margin: auto 5px;
}
.notification-transition-container .notification-message-container h4{
    font-size: 15px;
    color: white;
    width: auto;
    margin:0 10px;
}
@media screen and (max-width: 1165px) {
    .notification-transition-container {
        right: 50px;
    }
}
@media screen and (max-width: 700px) {
    .notification-transition-container {
        display: grid;
        width: 50%;
        margin: 0 auto;
        right: 0;
        left: 0;
    }
    .notification-transition-container .close-notification .close-icon{

    }
    .notification-transition-container .notification-icon-container{
        width:100%;
        height: auto;
        text-align: center;
    }
    .notification-transition-container .notification-message-container{
        padding: 5px;
    }
}

@media screen and (min-width: 700px) {
    .notification-container{
        display: flex;
    }
}

/*TeachingDayPopup*/

.t-day-main{
    width: 55%;
    height: auto;
    max-height: 550px;
    overflow-y: auto;
    overflow-x: hidden;
    margin:30px auto;
    padding: 15px;
    background: var(--LightBlue);
    color: white;
    border-radius: 20px;
    border: 2px solid var(--DarkBlue);
    box-shadow: 1px 1px 5px var(--DarkBlue);
}

.t-day-main .t-day-close-button-container{
    width: 100%;
    height:40px;
    margin: 0;
}
.t-day-main .t-day-title{
    width: 100%;
    padding: 15px;
    margin-top: 0;
    margin-bottom: 30px;
}
@media screen and (max-width: 850px) {
    .t-day-main .t-day-title{
        padding: 5px;
    }
}
.t-day-main .t-day-title h3{
    margin: 0;
    text-align: center;
    color: white;
}
.t-day-main .t-day-btn-container{
    width: 100%;
    padding: 10px;
}
.t-day-main .t-day-btn-container button{
    width: 100px;
    height: 30px;
    margin-left: 0;
}
.t-day-main .t-day-table-container{
    width: 100%;
    min-height: 300px;
}
.t-day-main .t-day-table-container table{
    width: 95%;
    margin: 0 auto;
}
.t-day-main .t-day-table-container table thead th{
    padding:5px;
}
.t-day-main .t-day-table-container table tbody tr td{
    padding: 5px 5px;
}
@media screen and (max-width: 850px) {
    .t-day-main .t-day-table-container{
        display: none;
    }
    .t-day-main .mobile-container{
        padding: 15px;
    }
    .t-day-main .mobile-container .form-items .form-children{
        margin:0;
        padding: 5px !important;
    }
    .t-day-main .mobile-container .form-items .form-children label{
        min-width: 100px;
    }
    .t-day-main .mobile-container .form-items .form-children .t-day-mobile-action-btn-container{
        display: inline-flex;
        padding: 5px 0;
        margin-bottom: 10px;
    }
}
@media screen and (min-width: 850px) {
    .t-day-main .mobile-container{
        display: none;
    }
}